import React from "react";
import styled from "styled-components";

const Contact = () => {
  const Container = styled.div`
    height: 100vh;
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;
  `;

  return (
    <Container id="contact">
      <h1>Get in touch</h1>
      <h3>Email</h3>
      <h3>Linked in</h3>
      <h3>Github</h3>
    </Container>
  );
};

export default Contact;
