import React from "react";
import styled from "styled-components";

const Projects = () => {
  const Container = styled.div`
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: red;
    color: white;
  `;

  return (
    <Container id="projects">
      <h1>Some of my projects</h1>
    </Container>
  );
};

export default Projects;
